/* Apple-like system font */
.login-wrapper {
  min-height: 100vh;
  display: grid;
  place-items: center;
  background: #f5f5f7;
  padding: 0px;
  font-family: -apple-system, BlinkMacSystemFont, "SF Pro Text",
    "SF Pro Display", "Helvetica Neue", Arial, sans-serif;
}

.login-card {
  background: #ffffff;
  width: 100%;
  max-width: 420px;
  padding: 32px;
  border-radius: 22px;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.12);
  animation: fadeSlide 0.6s ease;
}

.login-card h1 {
  font-size: 26px;
  font-weight: 600;
  margin-bottom: 24px;
  text-align: left;
}

.login-card input {
  width: 100%;
  padding: 14px 16px;
  font-size: 16px;
  border-radius: 10px;
  border: 1px solid #d2d2d7;
  margin-bottom: 18px;
  box-sizing: border-box;
}

.login-card input:focus {
  outline: none;
  border-color: #0071e3;
}

.login-card button {
  width: 100%;
  padding: 14px;
  font-size: 16px;
  font-weight: 500;
  color: #fff;
  background: #000;
  border: none;
  border-radius: 12px;
  cursor: pointer;
  transition: transform 0.15s ease, opacity 0.15s ease;
}

.login-card button:hover {
  opacity: 0.9;
}

.login-card button:active {
  transform: scale(0.98);
}

/* Modal */
.modal-backdrop {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.45);
  backdrop-filter: blur(3px);
  /* reduced */
  display: grid;
  place-items: center;
  padding: 16px;
  z-index: 10;
}

.modal-card {
  background: #ffffff;
  width: 100%;
  max-width: 380px;
  padding: 38px;
  padding-block: 48px;
  border-radius: 22px;
  box-shadow: 0 20px 20px rgba(0, 0, 0, 0.10);
  animation: fadeSlide 0.5s ease;
}

.modal-card p {
  font-size: 16px;
  font-weight: 500;
  color: #1d1d1f;
  margin-bottom: 18px;
  text-align: center;
}

.modal-card input {
  width: 100%;
  padding: 14px 0;
  font-size: 20px;
  letter-spacing: 6px;
  text-align: center;
  border-radius: 12px;
  border: 1px solid #d2d2d7;
  box-sizing: border-box;
  font-family: inherit;
}

.modal-card input:focus {
  outline: none;
  border-color: #0071e3;
}

.modal-card {
  position: relative;
}

.modal-close {
  position: absolute;
  top: 12px;
  right: 12px;
  width: 26px;
  height: 26px;
  border-radius: 50%;
  border: none;
  background: #e5e5ea;
  color: #1d1d1f;
  font-size: 18px;
  line-height: 1;
  cursor: pointer;
  display: grid;
  place-items: center;
  transition: background 0.15s ease, transform 0.15s ease;
}

.modal-close:hover {
  background: #d1d1d6;
}

.modal-close:active {
  transform: scale(0.92);
}

/* Backdrop */
.modal-backdrop {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.45);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

/* Card */
.apple-modal {
  background: #fff;
  border-radius: 18px;
  padding: 32px;
  width: 100%;
  max-width: 420px;
  box-shadow:
    0 20px 40px rgba(0, 0, 0, 0.2),
    0 2px 6px rgba(0, 0, 0, 0.1);
  text-align: center;
}

/* Text */
.modal-title {
  font-size: 20px;
  font-weight: 600;
  margin-bottom: 14px;
}

.modal-text {
  font-size: 15px;
  color: #1d1d1f;
  line-height: 1.5;
}

/* Actions */
.stay-logged-actions {
  display: flex;
  justify-content: center;
  gap: 12px;
  margin-top: 28px;
}

/* Buttons */
.apple-btn {
  min-width: 90px;
  padding: 8px 18px;
  border-radius: 10px;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.15s ease;
}

/* Primary */
.apple-btn.primary {
  background: #0071e3;
  color: #fff;
  border: none;
}

.apple-btn.primary:hover {
  background: #0077ed;
}

.apple-btn.primary:active {
  transform: scale(0.97);
}

/* Secondary */
.apple-btn.secondary {
  background: #f5f5f7;
  color: #1d1d1f;
  border: 1px solid #d2d2d7;
}

.apple-btn.secondary:hover {
  background: #eaeaef;
}

.apple-btn.secondary:active {
  transform: scale(0.97);
}



/* Animations */
@keyframes fadeSlide {
  from {
    opacity: 0;
    transform: translateY(20px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* =========================
   MOBILE ADJUSTMENTS
========================= */
@media (max-width: 768px) {

  .login-card {
    padding: 24px;
    border-radius: 18px;
    width: 80%;
    margin: 10px;
  }

  .login-card h1 {
    font-size: 22px;
  }
}